<template>
  <div style="position:relative">
    <h4 :style="{color: labelcolor? $vuetify.theme.defaults.light.primary: ''}">{{headertext || $t('label_The_recipient')}}</h4>
    <div :style="{'border-bottom':labelcolor? '2px solid '+ $vuetify.theme.defaults.light.primary:'1px solid black'}"
         class="dvinput">
      <span class="d-flex span1">
        <span class="person"
              v-for="(item,index) in showlist"
              :key='index+item'>
          <v-chip :close='true'
                  :disabled='readonly'
                  @click:close='chipclose(item,index)'>
            {{item}}</v-chip>
        </span>
        <span class="property"
              v-for="(item,index) in showpropertylist"
              :key="index">
          {{item.property}}
        </span>
        <input type="text"
               class="input1"
               @input="input"
               @blur="blur"
               @focus="focus"
               v-model="inputvalue">
        <span @click="changeselect">
          <v-icon class="material-icons">expand_more</v-icon>
        </span>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    selectlist: { //所有下拉框列表
      type: Array
    },
    showlist: { //用户选择的主收件人
      type: Array
    },
    propertylist: { // 用户选择的字段属性
      type: Array
    },
    headertext: {
      type: String
    },
    readonly: {
      type: Boolean,
      default: false
    },
    iconid: {
      type: String,
      default: 'meass'
    }
  },
  data () {
    return {
      labelcolor: false,
      inputvalue: '',
      dialog: false,
      tab: null,
      items: [
        { tab: '功能变量' },
        { tab: '系统变量' },
      ],
      variatelist: [],
      showpropertylist: [],//
      showselectlist: [],//显示的下拉框
    };
  },
  computed: {},
  created () { },
  watch: {
    propertylist: {
      handler (v) {
        this.showpropertylist = v
        this.getvariatelist(this.showpropertylist, this.variatelist)
      }, deep: true
    }
  },
  mounted () {
    this.getvariatelist()
    this.showselectlist = this.selectlist
  },

  methods: {
    changeselect () { //打开下拉列表
      this.$emit('changeselect')
    },
    chipclose (item, index) {
      this.showlist.splice(index, 1)
    },
    blur (v) { //失去焦点  添加内容
      this.labelcolor = !this.labelcolor
      if (this.inputvalue && !this.showlist.includes(this.inputvalue)) {
        this.showlist.push(this.inputvalue)
        this.inputvalue = ''
      }
      else {
        if (!this.inputvalue) {
        }
        else {
          this.$store.commit('newsTips', this.$t('message_Do_not_add_repeatedly'))
          this.inputvalue = ''
        }
      }
    },
    input () { //input修改时根据内容弹出下拉框
      var showselectlist = []
      if (this.inputvalue) {
        this.selectlist.map(item => {
          if (item.name.indexOf(this.inputvalue) != -1) {
            showselectlist.push(item)
          }
        })
      }
      this.showselectlist = showselectlist
      // this.$emit('getshowlist', this.showlist)
    },
    focus () { //聚焦
      this.labelcolor = !this.labelcolor
    },
  },
};
</script>

<style scoped>
.hideorshow {
  display: none;
}
.selectbox {
  position: absolute;
  top: 75px;
  z-index: 1;
  background: #ffffff;
  width: 50%;
}
.dvinput {
  height: 40px;
  line-height: 40px;
}
.ptbox {
  display: flex;
  flex-direction: column;
  height: 50px;
  border-bottom: 2px solid #eeeef0;
}
.divbgd {
  background-color: #fff7e6;
}
.input1 {
  height: 40px;
  line-height: 40px;
  border: 0;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  flex-grow: 1;
}
.property {
  font-size: 22px;
}
.span1 {
  width: 100%;
  display: flex;
}
</style>
